import React, {Component} from 'react';
import data from '../data'
import Menu from "../components/Menu";
import Popup from "../components/Popup";
import Category from "../components/Category";
import Emitter from "../helpers/Emitter";

class Portfolio extends Component {
  constructor(props) {
    super(props);
    this.state = {
      open: false,
      selectedCat: data.home.category[0].id,
    };
    Emitter.add('CHANGE_CATEGORY', this.handleCatChange);
  }

  componentWillUnmount() {
    Emitter.remove('CHANGE_CATEGORY', this.handleCatChange);
  }

  handleClick = () => {
    this.setState({open: !this.state.open})
  };

  handleCatChange = (id) => {
    this.setState({selectedCat: id})
  };

  render() {
    const {selectedCat} = this.state;
    const Items = data.home.portfolio.filter((item) => {
      return item.category.indexOf(selectedCat) > -1
    });
    const {selected} = this.state;
    return (
      <div className="main-content">
        <div className='page-border-for-about border-left-for-about'/>
        <div className='page-border-for-about border-right-for-about'/>
        <div className='page-border-for-about border-top-for-about'/>
        <div className='page-border-for-about border-bottom-for-about'/>
        <Menu/>
        <div className="sections">
          <section id='portfolio' className='section portfolio-section border-d'>
            <div className='section-block portfolio-block'>
              <div className='container-fluid'>
                <div>
                  <div className='section-header'>
                    <h2>My <strong className='color'>Works</strong></h2>
                  </div>
                  <ul className='portfolio-filters'>
                    <Category/>
                  </ul>
                </div>
                <ul className='portfolio-items'>
                  {Items.map((item) => (
                    <li key={item.id}>
                      <div className='inner' onClick={this.handleClick}>
                        <img src={item.image} alt=""/>
                        <div className='overlay'>
                          <a className="view-project" style={{cursor: "pointer"}}>view project</a>
                        </div>
                      </div>
                    </li>
                  ))}
                </ul>
              </div>
            </div>
            {this.state.open && <Popup/>}
          </section>
        </div>
      </div>
    );
  }
}

export default Portfolio;