import React, {Component} from "react";
import {Link} from "react-router-dom";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loader: false,
    }
  }
  onLoading = () => {
    this.setState({loader: true})
  };
  render() {
    const {loader} = this.state
    return (
      <div onLoad={this.onLoading}>
        {loader && (
          <div id="preloader">
            <div className="loader"/>
            <div className="left"/>
            <div className="right"/>
          </div>
        )}
        <div className='main-content'>
          <div className='page-border border-left'/>
          <div className='page-border border-right'/>
          <div className='page-border border-top'/>
          <div className='page-border border-bottom'/>
          <Link to='#' className='menu-btn'>< span className='lines'>< span className='l1'/><span
            className='l2'/>< span
            className='l3'/></span>
          </Link>
          <div className='menu'>
            <div className='inner' style={{color: "#333"}}>
              <ul className='menu-items'>
                <li>
                  <Link to='/' className='section-toggle' data-section='intro'>
                    Home
                  </Link>
                </li>

                <li>
                  <Link to='/about' className='section-toggle' data-section='about'>
                    About
                  </Link>
                </li>

                <li>
                  <Link to='/resume' className='section-toggle' data-section='resume'>
                    Resume
                  </Link>
                </li>

                <li>
                  <Link to='/portfolio' className='section-toggle' data-section='portfolio'>
                    Portfolio
                  </Link>
                </li>


                <li>
                  <Link to='/contact' className='section-toggle' data-section='contact'>
                    Contact
                  </Link>
                </li>
              </ul>
            </div>
          </div>
          <div className='animation-block'/>
          <div className='sections'>
            <div id='intro' className='section section-main active'>
              <div className='container-fluid'>
                <div className='v-align'>
                  <div className='inner' style={{backgroundColor: "#2D3986"}}>
                    <div className='intro-text'>
                      <h1>I'm John Doe</h1>
                      <p>
                        A Friend, Dude and Buddy From Earth
                      </p>
                      <div className='intro-btns'>
                        <Link to='/about' className='btn-custom section-toggle' data-section='about'>
                          Discover More
                        </Link>
                        <Link to='/contact' className='btn-custom section-toggle' data-section='contact'>
                          Hire Me
                        </Link>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default App;